<template>
<body id="poster">
  <el-container>
    <el-form
      ref="form"
      :model="form"
      :rules="formRules"
      class="login-container"
      label-position="left"
      label-width="0px"
    >
      <h3 class="login_title"></h3>
      <el-form-item prop="username">
        <el-input type="text" v-model="form.username" auto-complete="off" placeholder="" v-on:keyup.enter.native="onSubmit"></el-input>
      </el-form-item>

      <el-form-item prop="password">
        <el-input type="password" v-model="form.password" auto-complete="off" placeholder="" v-on:keyup.enter.native="onSubmit"></el-input>
      </el-form-item>

      <el-form-item style="width: 100%">
        <el-button
          type="primary"
          style="width: 100%;background: #505458;border: none"
          v-on:click="onSubmit"
        ></el-button>
      </el-form-item>
    </el-form>
  </el-container>
</body>
</template>

<script>
import api from "@/assets/api.js";
import axios from "axios";
import qs from "qs";
export default {
  data() {
    return {
      //
      form: {
        username: "",
        password: "",
      },
      //
      formRules: {
        //
        username: [
          { required: true, message: "", trigger: "blur" },
        ],
        //
        password: [
          { required: true, message: "", trigger: "blur" },
        ],
      },
    };
  },
  methods: {
    async onSubmit() {
      //
      this.$refs.form.validate((valid) => {
        if (!valid) return;
      });

      try {
        const res = await axios.post(
          `${api.API_URL}/login/login`,
          qs.stringify(this.form)
        );
        if (res.status == 200) {
          window.sessionStorage.setItem("token", res.data.data["token"]);
          this.$router.push("/home");
          this.$message.success("");
        }
      } catch (err) {
        console.log(err);
        this.$message.error("");
      }
    },
  },
};
</script>


<style scoped>
#poster {
  height: 100%;
  width: 100%;
  background-size: cover;
  position: fixed;
}

body {
  color: rgba(255, 255, 255, 0.65);
  background-color: #24292e;
    /*background-image: url(../../assets/img/star-bg.svg),*/
    /*linear-gradient(#191c20, #24292e 15%);*/
  background-repeat: repeat-x;
  background-position: center 0, 0 0, 0 0;
}

.login-container {
  border-radius: 15px;
  background-clip: padding-box;
  margin: 150px auto;
  width: 300px;
  padding: 35px 35px 15px 35px;
  background: #fff;
  border: 1px solid #eaeaea;
}

.login_title {
  letter-spacing: 10px;
  margin: 0px auto 40px auto;
  text-align: center;
  color: #505458;
}
</style>
