<template>
  <el-card class="form-container" shadow="never" v-loading="loading">
    <!-- <div class="procedures" v-loading="loading">
      <el-steps :space="400" :active="0" finish-status="success">
        <el-step title=""></el-step>
        <el-step title=" 2"></el-step>
        <el-step title=" 3"></el-step>
      </el-steps>
    </div> -->
    <el-form
      :model="ruleForm"
      :rules="rules"
      ref="ruleForm"
      label-width="120px"
      class="demo-ruleForm"
    >
      <el-form-item label="" prop="name">
        <el-input v-model="ruleForm.name"></el-input>
      </el-form-item>
      <el-form-item
        label=""
        prop="categoryId"
        style="text-align: left"
      >
        <el-select v-model="ruleForm.categoryId" placeholder="">
          <el-option label="" value="1"></el-option>
          <el-option label="" value="2"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="details">
        <el-input v-model="ruleForm.details"></el-input>
      </el-form-item>

      <el-form-item label="" prop="poster">
        <!--    -->
        <el-upload
          class="upload"
          action
          :drag="true"
          :multiple="true"
          :file-list="images"
          :http-request="uploadHttp"
          :before-upload="beforeAvatarUpload"
          :on-remove="handleRemove"
          style="text-align: left"
        >
          <i class="el-icon-plus avatar-uploader-icon"></i>
          <p id="img-context"></p>
          <div class="el-upload__tip" slot="tip">
            jpg/jpeg/png5MB
          </div>
        </el-upload>
      </el-form-item>

      <el-form-item label="" prop="minPrice" style="width: 50%">
        <el-input v-model="ruleForm.minPrice"></el-input>
      </el-form-item>
      <el-form-item label="" prop="maxPrice" style="width: 50%">
        <el-input v-model="ruleForm.maxPrice"></el-input>
      </el-form-item>
      <!-- <el-form-item label="" prop="region">
        <el-select v-model="ruleForm.region" placeholder="">
          <el-option label="0" value="shanghai"></el-option>
          <el-option label="1" value="beijing"></el-option>
        </el-select>
      </el-form-item> -->

      <el-form-item
        label=""
        prop="weight"
        style="text-align: left"
      >
        <el-radio-group v-model="ruleForm.weight">
          <el-radio label="0"></el-radio>
          <el-radio label="1"></el-radio>
          <el-radio label="2"></el-radio>
          <el-radio label="3"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="" prop="city">
        <el-input v-model="ruleForm.city"></el-input>
      </el-form-item>
      <el-form-item label="" prop="address">
        <el-input type="textarea" v-model="ruleForm.address"></el-input>
      </el-form-item>
      <el-form-item label="" required>
        <el-col :span="11">
          <el-form-item prop="dayStart">
            <el-date-picker
              type="date"
              placeholder=""
              v-model="ruleForm.dayStart"
              style="width: 100%"
              format="yyyy  MM  dd  HH  mm  ss "
              value-format="yyyy-MM-dd HH:mm:ss"
            ></el-date-picker>
          </el-form-item>
        </el-col>
      </el-form-item>
      <el-form-item label="" required>
        <el-col :span="11">
          <el-form-item prop="dayEnd">
            <el-date-picker
              type="date"
              placeholder=""
              v-model="ruleForm.dayEnd"
              style="width: 100%"
              format="yyyy  MM  dd  HH  mm  ss "
              value-format="yyyy-MM-dd HH:mm:ss"
            ></el-date-picker>
          </el-form-item>
        </el-col>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm')"
          ></el-button
        >
        <el-button @click="resetForm('ruleForm')"></el-button>
      </el-form-item>
    </el-form>
  </el-card>
</template>

<script>
import ossClient from "../assets/config/aliyun.oss.client";
import axios from "axios";
import api from "@/assets/api.js";
import qs from "qs";

export default {
  name: "",
  props: [""],
  data() {
    return {
      loading: true,
      //
      images: [],
      uploadConf: {
        region: null,
        accessKeyId: null,
        accessKeySecret: null,
        bucket: null,
      },
      ruleForm: {
        //prop
        name: "",
        categoryId: "",
        poster: "",
        details: "",
        minPrice: "",
        maxPrice: "",
        weight: -1,
        city: "",
        address: "",
        dayStart: "",
        dayEnd: "",
      },
      rules: {
        name: [
          { required: true, message: "", trigger: "blur" },
          { min: 3, max: 5, message: " 3  5 ", trigger: "blur" },
        ],
        categoryId: [
          { required: true, message: "", trigger: "change" },
        ],
        details: [
          { required: true, message: "", trigger: "blur" },
        ],
        poster: [
          { required: true, message: "", trigger: "blur" },
        ],
        minPrice: [
          { required: true, message: "", trigger: "blur" },
        ],
        maxPrice: [
          { required: true, message: "", trigger: "blur" },
        ],
        city: [{ required: true, message: "", trigger: "blur" }],
        dayStart: [
          {
            type: "string",
            required: true,
            message: "",
            trigger: "change",
          },
        ],
        dayEnd: [
          {
            type: "string",
            required: true,
            message: "",
            trigger: "change",
          },
        ],

        weight: [
          {
            required: true,
            message: "",
            trigger: "change",
          },
        ],
        address: [
          { required: true, message: "", trigger: "blur" },
        ],
      },
    };
  },

  components: {},

  computed: {},

  beforeMount() {},

  mounted() {
    setTimeout(() => {
      this.loading = false;
    }, 500);
  },

  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (!valid || this.ruleForm.poster == "") {
          console.log(this.ruleForm.poster);
          console.log("error submit!!");
          this.$message.error("");
          return false;
        } else {
          try {
            console.log(this.ruleForm.dayStart);
            console.log("mounted");
            const res = await axios.post(
              `${api.API_URL}/show/create`,
              this.ruleForm, 
            );
            console.log(res);
            if (res.data.code == 200) {
              this.$message.success(",");
              // 
               setTimeout(() => {
                this.loading = false;
              }, 500);
              console.log(res.data.data);
              this.$router.push('/show')
            }
          } catch (err) {
            console.log(err);
            this.$message.error("");
          }
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },

    /**
     * 
     */
    async init() {
      //token  
      this.uploadConf.region = "oss-cn-shanghai";
      this.uploadConf.accessKeyId = "LTAI4FzMDhgBN9LMBr71T3Ny";
      this.uploadConf.accessKeySecret = "hTPgQQSyBgEDnfMNe06RPf8ecDafpz";
      this.uploadConf.bucket = "tongji-boying";
    },
    /**
     * OSS
     */
    uploadHttp({ file }) {
      this.init();
      const { imgName } = "ALIOSS_IMG_";
      const fileName = `${imgName}/${Date.parse(new Date())}`; //
      ossClient(this.uploadConf)
        .put(fileName, file, {
          ContentType: "image/jpeg",
        })
        .then(({ res, url, name }) => {
          if (res && res.status === 200) {
            console.log(`OSS`, res, url, name);
            console.log(url);
            this.ruleForm.poster = url;
          }
        })
        .catch((err) => {
          console.log(`OSS`, err);
        });
    },

    /**
     * 
     */
    beforeAvatarUpload(file) {
      const isJPEG = file.name.split(".")[1] === "jpeg";
      const isJPG = file.name.split(".")[1] === "jpg";
      const isPNG = file.name.split(".")[1] === "png";
      const isLt500K = file.size / 1024 / 1024 / 5 < 2;
      if (!isJPG && !isJPEG && !isPNG) {
        this.$message.error(" JPEG/JPG/PNG !");
      }
      if (!isLt500K) {
        this.$message.error(" 5MB!");
      }
      return (isJPEG || isJPG || isPNG) && isLt500K;
    },

    /**
     * 
     */
    handleRemove(file, fileList) {
      console.log(``, fileList);
    },
  },

  watch: {},
};
</script>
<style scoped>
/* .procedures {
  text-align: left;
  margin: 50px;
  margin-left: 200px;
} */

.form-container {
  margin: 50px;
  margin-left: 200px;
  width: 800px;
}
</style>