<template>
  <div>
    <el-card class="operate-container" shadow="never" style="text-align: left">
      <i class="el-icon-tickets"></i>
      <span></span>
      <div class="table-container">
        <el-table
          :key="key"
          :data="
            tableData.filter(
              (data) =>
                !search ||
                data.name.toLowerCase().includes(search.toLowerCase())
            )
          "
          v-loading="loading"
          style="width: 100%"
        >
          <el-table-column label="" prop="showId"></el-table-column>
          <el-table-column label="" prop="name"></el-table-column>
          <el-table-column label="" prop="category"> </el-table-column>
          <el-table-column label="" align="center">
            <template slot-scope="scope">
              <el-image
                style="width: 100px; height: 100px"
                :src="scope.row.poster"
                :preview-src-list="[scope.row.poster]"
              ></el-image>
            </template>
          </el-table-column>
          <el-table-column
            label=""
            prop="minPrice"
          ></el-table-column>
          <el-table-column
            label=""
            prop="maxPrice"
          ></el-table-column>
          <el-table-column label="" prop="address"></el-table-column>
          <el-table-column label="" width="160" align="center">
            <template slot-scope="scope">{{
              scope.row.dayStart | formatDateTime
            }}</template>
          </el-table-column>
          <el-table-column label="" width="160" align="center">
            <template slot-scope="scope">{{
              scope.row.dayEnd | formatDateTime
            }}</template>
          </el-table-column>

          <el-table-column label="">
            <template slot-scope="scope">
              <el-button
                size="mini"
                type="danger"
                @click="handleDelete(scope.$index, scope.row)"
                ></el-button
              >
              <el-button
                size="mini"
                style="margin-left: 0"
                @click="handleClick(scope.row)"
                ></el-button
              >
            </template>
          </el-table-column>
          <!--  <el-table-column label="" prop="name"> </el-table-column>
    <el-table-column label="" prop="dayStart">
    </el-table-column>
    <el-table-column label="" prop="poster"> </el-table-column>
    <el-table-column label="" prop="showAddress"> </el-table-column> -->
          <el-table-column align="center">
            <template slot="header" slot-scope="scope">
              <el-input
                v-model="search"
                size="mini"
                placeholder=""
              />
            </template>
          </el-table-column>
        </el-table>
        <el-dialog
          title=""
          width="30%"
          :visible.sync="dialogVisible"
          :before-close="handleClose"
        >
          <span>{{ dialogInfo }}</span>
          <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false"> </el-button>
            <el-button type="primary" @click="dialogVisible = false"
              > </el-button
            >
          </span>
        </el-dialog>
      </div>
    </el-card>
  </div>
</template>


<script>
import axios from "axios";
import api from "@/assets/api.js";
import qs from "qs";
import { formatDate } from "@/utils/date";

const fields = [
  { label: "", prop: "showId" },
  { label: "", prop: "name" },
  { label: "", prop: "categoryId" },
  { label: "", prop: "category" },

  { label: "", prop: "poster" },
  { label: "", prop: "details" },
  { label: "", prop: "minPrice" },
  // { label: "", prop: "showMinPrice" },
  { label: "", prop: "maxPrice" },
  // { label: "", prop: "showWeight" },
  // { label: "", prop: "showCity" },
  { label: "", prop: "address" },
  // { label: "", prop: "showTime" },
  { label: "", prop: "dayStart" },
  { label: "", prop: "dayEnd" },
];

export default {
  async mounted() {
    this.reload();
  },
  data() {
    return {
      tableData: [],
      dialogVisible: false, //
      search: "",
      key: 1, // table key
      formThead: fields, //  Default header
      loading: true,
      dialogInfo: "",
    };
  },
  methods: {
    //   
    handleClick(info) {
      console.log(info);
      this.dialogVisible = true;
      this.dialogInfo = info;
      console.log(this.dialogVisible);
    },
    dialogVisibles(v) {
      this.dialogVisible = v;
      console.log(v);
    },
    handleClose(done) {
      this.$confirm("")
        .then((_) => {
          done();
        })
        .catch((_) => {});
    },
    async handleDelete(index, row) {
      console.log(row.showId);
      try {
        console.log("mounted");
        const res = await axios.post(
          `${api.API_URL}/show/delete/` + row.showId,
          {
            headers: {
              Authorization: "Bearer " + sessionStorage.getItem("token"),
            },
          }
        );
        console.log("test");
        console.log(res);
        if (res.data.code == 200) {
          this.$message.success("");
        }
      } catch (err) {
        console.log(err);
      }
      this.tableData.splice(index, 1);
      console.log(index, row);
    },
    async reload() {
      try {
        console.log("mounted");
        const res = await axios.get(`${api.API_URL}/show/list`, {
          headers: {
            Authorization: "Bearer " + sessionStorage.getItem("token"),
          },
        });
        console.log(res);
        if (res.data.code == 200) {
          this.tableData = res.data.data;
          for (var i = 0; i < this.tableData.length; i++) {
            this.getCategory(this.tableData[i].categoryId, i);
          }
          // console.log(this.tableData)
          setTimeout(() => {
            this.loading = false;
          }, 500);
        }
      } catch (err) {
        console.log(err);
      }
    },
    async getCategory(categoryId, i) {
      try {
        console.log("mounted");
        const res = await axios.get(
          `${api.API_URL}/category/list/` + categoryId,
          {
            headers: {
              Authorization: "Bearer " + sessionStorage.getItem("token"),
            },
          }
        );
        console.log(res);
        if (res.data.code == 200) {
          this.tableData[i].category = res.data.data.name;
          console.log(this.tableData[i].category);
          setTimeout(() => {
            this.loading = false;
          }, 500);
        }
      } catch (err) {
        console.log(err);
      }
    },

    /**
     * 
     * @param arr
     */
    getMaxLength(arr) {
      return arr.reduce((acc, item) => {
        if (item) {
          let calcLen = this.getTextWidth(item);
          if (acc < calcLen) {
            acc = calcLen;
          }
        }
        return acc;
      }, 0);
    },
    /**
     * spanspan width px
     * @param valArr
     */
    getTextWidth(str) {
      let width = 0;
      let html = document.createElement("span");
      html.innerText = str;
      html.className = "getTextWidth";
      document.querySelector("body").appendChild(html);
      width = document.querySelector(".getTextWidth").offsetWidth;
      document.querySelector(".getTextWidth").remove();
      return width;
    },
  },
  filters: {
    formatDateTime(time) {
      if (time == null || time === "") {
        return "N/A";
      }
      let date = new Date(time);
      return formatDate(date, "yyyy-MM-dd hh:mm:ss");
    },
  },
  watch: {
    /**
     * data
     * @param valArr
     */
    data(valArr) {
      const _this = this;
      this.formThead = fields.map(function (value) {
        const arr = valArr.map((x) => x[value.prop]); // 
        arr.push(value.label); // 
        value.width = _this.getMaxLength(arr) + 20; //  + ()
        return value;
      });
    },
  },
};
</script>

<style scoped>
.el-button {
  text-size-adjust: auto;
}
</style>
