<template>
  
  <div class="app-container">
    <!-- <el-card class="filter-container" shadow="never" align="left">
      <div>
        <i class="el-icon-search"></i>
        <span></span>
        <el-button
          style="float: right"
          type="primary"
          @click="handleSearchList()"
          size="small"
        >
          
        </el-button>
        <el-button
          style="float: right; margin-right: 15px"
          @click="handleResetSearch()"
          size="small"
        >
          
        </el-button>
      </div>
      <div style="margin-top: 15px">
        <el-form
          :inline="true"
          :model="listQuery"
          size="small"
          label-width="140px"
        >
          <el-form-item label="">
            <el-input
              v-model="listQuery.keyword"
              class="input-width"
              placeholder=""
              clearable
            ></el-input>
          </el-form-item>
        </el-form>
      </div>
    </el-card> -->
    <el-card class="operate-container" shadow="never" style="text-align: left">
      <i class="el-icon-tickets"></i>
      <span></span>
      <el-button
        size="mini"
        class="btn-add"
        @click="handleAdd()"
        style="float:right"
        ></el-button
      >
    </el-card>
    <div class="table-container">
      <el-table
        ref="adminTable"
        :key="key"
        :data="
          list.filter(
            (data) =>
              !search ||
              data.username.toLowerCase().includes(search.toLowerCase())
          )
        "
        style="width: 100%"
        v-loading="listLoading"
        border
      >
        <el-table-column label="id" width="100" align="center">
          <template slot-scope="scope">{{ scope.row.userId }}</template>
        </el-table-column>
        <el-table-column label="" align="center">
          <template slot-scope="scope">{{ scope.row.username }}</template>
        </el-table-column>
        <el-table-column label="" align="center">
          <template slot-scope="scope">{{ scope.row.phone }}</template>
        </el-table-column>
        <el-table-column label="" align="center">
          <template slot-scope="scope">{{ scope.row.realName }}</template>
        </el-table-column>
        <el-table-column label="" width="160" align="center">
          <template slot-scope="scope">{{ scope.row.email }}</template>
        </el-table-column>
        <el-table-column label="" width="160" align="center">
          <template slot-scope="scope">{{
            scope.row.createTime | formatDateTime
          }}</template>
        </el-table-column>
        <el-table-column label="" width="140" align="center">
          <template slot-scope="scope">
            <el-switch
              @change="handleStatusChange(scope.$index, scope.row)"
              :active-value="true"
              :inactive-value="false"
              v-model="scope.row.userstatus"
            >
            </el-switch>
          </template>
        </el-table-column>
        <el-table-column label="" width="180" align="center">
          <template slot-scope="scope">
            <!-- <el-button
              size="mini"
              type="text"
              @click="handleUpdate(scope.$index, scope.row)"
            >
              
            </el-button> -->
            <el-button
              size="mini"
              type="text"
              @click="handleDelete(scope.$index, scope.row)"
              >
            </el-button>
          </template>
        </el-table-column>
        <el-table-column align="center">
          <template slot="header" slot-scope="scope">
            <el-input
              v-model="search"
              size="mini"
              placeholder=""
            />
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!-- <div class="pagination-container">
      <el-pagination
        background
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        layout="total, sizes,prev, pager, next,jumper"
        :current-page.sync="listQuery.pageNum"
        :page-size="listQuery.pageSize"
        :page-sizes="[10, 15, 20]"
        :total="total"
      >
      </el-pagination>
    </div> -->
    <el-dialog
      :title="isEdit ? '' : ''"
      :visible.sync="dialogVisible"
      width="40%"
    >
      <el-form :model="user" ref="adminForm" label-width="150px" size="small">
        <el-form-item label="">
          <el-input v-model="user.username" style="width: 250px"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="user.phone" style="width: 250px"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="user.realName" style="width: 250px"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="user.email" style="width: 250px"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input
            v-model="user.password"
            type="password"
            style="width: 250px"
          ></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-radio-group v-model="user.userstatus">
            <el-radio :label="true"></el-radio>
            <el-radio :label="false"></el-radio>
          </el-radio-group>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false" size="small"> </el-button>
        <el-button type="primary" @click="handleDialogConfirm()" size="small"
          > </el-button
        >
      </span>
    </el-dialog>
    <!-- <el-dialog title="" :visible.sync="allocDialogVisible" width="30%">
      <el-select
        v-model="allocRoleIds"
        multiple
        placeholder=""
        size="small"
        style="width: 80%"
      >
        <el-option
          v-for="item in allRoleList"
          :key="item.id"
          :label="item.name"
          :value="item.id"
        >
        </el-option>
      </el-select>
      <span slot="footer" class="dialog-footer">
        <el-button @click="allocDialogVisible = false" size="small"
          > </el-button
        >
        <el-button
          type="primary"
          @click="handleAllocDialogConfirm()"
          size="small"
          > </el-button
        >
      </span>
    </el-dialog> -->
  </div>
</template>
<script>
import axios from "axios";
import api from "@/assets/api.js";
import qs from "qs";
import { formatDate } from "@/utils/date";
const defaultListQuery = {
  pageNum: 1,
  pageSize: 10,
  keyword: null,
};
const defaultUser = {
  userId: null,
  username: null,
  phone: null,
  realName: null,
  password: null,
  email: null,
  createTime: null,
  userstatus: true,
};
export default {
  name: "adminList",
  data() {
    return {
      key: 1, // table key
      search: "",
      listQuery: Object.assign({}, defaultListQuery),
      list: [],
      total: null,
      listLoading: false,
      dialogVisible: false,
      user: Object.assign({}, defaultUser),
      isEdit: false,
      allocDialogVisible: false,
      allocRoleIds: [],
      allRoleList: [],
      allocAdminId: null,
    };
  },
  created() {
    this.getList();
    this.getAllRoleList();
  },
  filters: {
    formatDateTime(time) {
      if (time == null || time === "") {
        return "N/A";
      }
      let date = new Date(time);
      return formatDate(date, "yyyy-MM-dd hh:mm:ss");
    },
  },
  methods: {
    handleResetSearch() {
      this.listQuery = Object.assign({}, defaultListQuery);
    },
    handleSearchList() {
      this.listQuery.pageNum = 1;
      this.getList();
    },
    handleSizeChange(val) {
      this.listQuery.pageNum = 1;
      this.listQuery.pageSize = val;
      this.getList();
    },
    handleCurrentChange(val) {
      this.listQuery.pageNum = val;
      this.getList();
    },

    async addUser(user) {
      user.createTime = new Date();
      try {
        const res = await axios.post(`${api.API_URL}/user/add`, user, {
          headers: {
            Authorization: "Bearer " + sessionStorage.getItem("token"),
          },
        });
        console.log(res);
        if (res.data.code == 200) {
          this.$message.success("");
          this.getList();
        }
      } catch (err) {
        console.log(err);
        this.$message.error("");
      }
    },
    handleAdd() {
      this.dialogVisible = true;
      this.isEdit = false;
      this.user = Object.assign({}, defaultUser);
    },

    async updateStatus(row) {
      try {
        const res = await axios.post(
          `${api.API_URL}/user/ChangeUserStatus/` + row.userId,
          {
            headers: {
              Authorization: "Bearer " + sessionStorage.getItem("token"),
            },
          }
        );
        console.log(res);
        if (res.data.code == 200) {
          this.$message.success("");
        }
      } catch (err) {
        console.log(err);
        this.$message.error("");
      }
      this.getList();
    },

    async handleStatusChange(index, row) {
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(() => {
          this.updateStatus(row);
        })
        .catch(() => {
          this.$message.info("");
          console.log("catch");
          this.getList();
        });
    },

    async deleteUser(row) {
      try {
        const res = await axios.post(
          `${api.API_URL}/user/delete/` + row.userId,
          {
            headers: {
              Authorization: "Bearer " + sessionStorage.getItem("token"),
            },
          }
        );
        console.log(res);
        if (res.data.code == 200) {
          this.$message.success("");
        }
      } catch (err) {
        console.log(err);
        this.$message.error("");
      }
      this.getList();
    },

    handleDelete(index, row) {
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(() => {
          this.deleteUser(row);
        })
        .catch(() => {
          this.$message.info("");
          console.log("catch");
          this.getList();
        });
    },
    handleUpdate(index, row) {
      this.dialogVisible = true;
      this.isEdit = true;
      this.user = Object.assign({}, row);
    },
    async updateUser(id, user) {
      try {
        const res = await axios.post(`${api.API_URL}/user/update/` + id, user, {
          headers: {
            Authorization: "Bearer " + sessionStorage.getItem("token"),
          },
        });
        console.log(res);
        if (res.data.code == 200) {
          this.$message.success("");
          this.getList();
        }
      } catch (err) {
        console.log(err);
        this.$message.error("");
      }
    },
    handleDialogConfirm() {
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      }).then(() => {
        if (this.isEdit) {
          this.updateUser(this.user.userId, this.user);
          this.dialogVisible = false;
        } else {
          this.addUser(this.user);
          this.dialogVisible = false;
        }
      });
    },
    handleAllocDialogConfirm() {
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      }).then(() => {
        let params = new URLSearchParams();
        params.append("adminId", this.allocAdminId);
        params.append("roleIds", this.allocRoleIds);
        allocRole(params).then((response) => {
          this.$message({
            message: "",
            type: "success",
          });
          this.allocDialogVisible = false;
        });
      });
    },
    //
    handleSelectRole(index, row) {
      this.allocAdminId = row.id;
      this.allocDialogVisible = true;
      this.getRoleListByAdmin(row.id);
    },
    async getList() {
      this.listLoading = true;
      try {
        const res = await axios.get(`${api.API_URL}/user/list`, {
          headers: {
            Authorization: "Bearer " + sessionStorage.getItem("token"),
          },
        });
        console.log(res);
        if (res.data.message == "") {
          this.list = null;
        }
        if (res.data.code == 200) {
          this.list = res.data.data;
        }
        setTimeout(() => {
          this.listLoading = false;
        }, 500);
      } catch (err) {
        console.log(err);
      }
      // fetchList(this.listQuery).then(response => {
      //   this.listLoading = false;
      //   this.list = response.data.list;
      //   this.total = response.data.total;
      // });
    },
    getAllRoleList() {
      // fetchAllRoleList().then(response => {
      //   this.allRoleList = response.data;
      // });
    },
    getRoleListByAdmin(adminId) {
      getRoleByAdmin(adminId).then((response) => {
        let allocRoleList = response.data;
        this.allocRoleIds = [];
        if (allocRoleList != null && allocRoleList.length > 0) {
          for (let i = 0; i < allocRoleList.length; i++) {
            this.allocRoleIds.push(allocRoleList[i].id);
          }
        }
      });
    },
  },
};
</script>
