<template>
  <div>
    <el-card class="operate-container" shadow="never" style="text-align: left">
      <i class="el-icon-tickets"></i>
      <span></span>
    </el-card>
    <div class="table-container">
      <el-table
        :key="key"
        :data="
          tableData.filter(
            (data) =>
              !search ||
              data.realStatus.toLowerCase().includes(search.toLowerCase())
          )
        "
        v-loading="loading"
        style="width: 100%"
      >
        <!-- <el-table-column type="expand">
          <template slot-scope="scope">
            <el-form
              label-position="left"
              inline
              class="demo-table-expand"
              v-for="fruit in formThead"
              :key="fruit.prop"
              :width="fruit.width"
              show-overflow-tooltip
            >
            <el-form-item >
              <span>{{scope.row[fruit.label]}}</span>
              <span>{{ scope.row[fruit.prop] }}</span>
            </el-form-item>
            </el-form>
          </template>
        </el-table-column> -->
        <el-table-column type="expand">
          <template slot-scope="props">
            <el-form label-position="left" inline class="demo-table-expand">
              <el-form-item label=":">
                <span>{{ props.row.orderId }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.userId }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.showId }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.realStatus }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.time | formatDateTime }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.money }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.showSessionId }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.payment }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.userDelete | formatUserDelete }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.ticketCount }}</span>
              </el-form-item>
            </el-form>
          </template>
        </el-table-column>
        <el-table-column label="" prop="orderId"> </el-table-column>
        <el-table-column label="" prop="userId"> </el-table-column>
        <el-table-column label="" prop="showId"> </el-table-column>
        <el-table-column label="" prop="realStatus"> </el-table-column>
        <el-table-column label="" width="160" align="center">
          <template slot-scope="scope">{{
            scope.row.time | formatDateTime
          }}</template>
        </el-table-column>

        <el-table-column label="" prop="money" align="center"> </el-table-column>
        <el-table-column align="center">
          <template slot="header" slot-scope="scope">
            <el-input
              v-model="search"
              size="mini"
              placeholder=""
            />
          </template>
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="danger"
              @click="Delete(scope.$index, scope.row)"
              ></el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import api from "@/assets/api.js";
import qs from "qs";
import { formatDate } from "@/utils/date";

const fields = [
  { label: "", prop: "orderId" },
  { label: "", prop: "userId" },
  { label: "", prop: "showId" },
  { label: "", prop: "status" },
  { label: "", prop: "realStatus" },
  { label: "", prop: "time" },
  { label: "", prop: "money" },

  //
  { label: "", prop: "showSessionId" },
  { label: "", prop: "payment" },
  { label: "", prop: "userDelete" },
  { label: "", prop: "ticketCount" },
];

export default {
  name: "",
  props: [""],
  data() {
    return {
      tableData: [],
      search: "",
      key: 1, // table key
      formThead: fields, //  Default header
      loading: true,
      OrderState: ["", "", ""],
    };
  },

  components: {},

  computed: {},

  beforeMount() {},

  mounted() {
    this.reload();
  },

  methods: {
    Delete(index,row) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(() => {
          this.handleDelete(row.orderId)
        }).catch(() => {
          this.$message.info("");
        });
    },
    async handleDelete(id) {
      try {
        console.log("mounted");
        const res = await axios.post(`${api.API_URL}/user/deleteOrder/` + id);
        console.log("test");
        console.log(res);
        if (res.data.code == 200) {
          this.$message.success("");
          this.reload();
        }
      } catch (err) {
        console.log(err);
      }
    },

    async reload() {
      try {
        console.log("mounted");
        const res = await axios.get(`${api.API_URL}/user/listOrders`, {
          headers: {
            Authorization: "Bearer " + sessionStorage.getItem("token"),
          },
        });
        console.log(res);
        if(res.data.message == ""){
          this.tableData=[];
        }
        if (res.data.code == 200) {
          this.tableData = res.data.data;
          for (var i = 0; i < this.tableData.length; i++) {
            if (this.tableData[i].status == 1) {
              this.tableData[i].realStatus = "";
            } else if (this.tableData[i].status == 2) {
              this.tableData[i].realStatus = "";
            } else {
              this.tableData[i].realStatus = "";
            }
          }
        }
        setTimeout(() => {
          this.loading = false;
        }, 500);
      } catch (err) {
        console.log(err);
      }
    },
  },
  filters: {
    formatDateTime(time) {
      if (time == null || time === "") {
        return "N/A";
      }
      let date = new Date(time);
      return formatDate(date, "yyyy-MM-dd hh:mm:ss");
    },

    formatUserDelete(value){
      if(value==true){
        return "";
      }
      else{
        return "";
      }
    }
  },

  watch: {},
};
</script>

<style scoped>
.demo-table-expand {
  font-size: 0;
}
/* css */
.demo-table-expand .label {
  width: 90px;
  color: #99a9bf;
}
.demo-table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  width: 50%;
}
</style>